<template>
	<div class="mainDiv">
		<div class=""></div>
		<div class="url-class">
			<ul>
				<li v-for="item in urlClasses" class="">{{item.name}}</li>
				<li @click="flagObj.newClassAdd='true'">新增</li>
			</ul>
			<div v-if="flagObj.newClassAdd" class="url-class-new">
				<input type="text" class="input-text" v-model="newUrlClassName" placeholder="请输入要增加的分类" />
				<button class="btn" @click="addUrlClass">确定</button><button class="btn" @click="flagObj.newClassAdd=0">取消</button>
			</div>
		</div>
		<div class="url-sites">
			<div class="site">新增</div>
		</div>

	</div>
</template>

<script>
import xyzApi from '@/api/priApi'

export default {
	data() {
		return {
			newUrlClassName: '',
			flagObj: {
				newClassAdd: false,
				newUrlAdd: false
			},
			urlClasses: [
				{
					id: 0,
					name: "视频",
				}
			],
			urlsActive: []

		}
	},
	created: function() {
		console.log(this.urlClasses);
	},
	methods: {
		addUrlClass: function() {
			this.flagObj.newClassAdd = !this.flagObj.newClassAdd;
			let params = {
				name: this.newName
			};
			console.log('tarData: ', params);
			service.addUrlClass(params).then(function(result) {
				console.log("添加成功");
			});
		},
		searchUrlList: function() {
			service.searchUrlList().then(function(result) {
				console.log(result);
				xyzApi.copy(result, this.urlClasses)
			})
		}
	}
}
</script>

<style scoped>
.mainDiv {

}
.url-class {
	margin-bottom: -6px;
	border: 1px solid #ccc;
}
.url-class ul {
	display: inline-block;
	margin: 0;
	padding: 0;
	width: 100%;
}
.url-class li {
	position: relative;
	float: left;
	padding: 10px;
	width: 100px;
	text-align: center;
	list-style: none;
	cursor: pointer;
}
.url-class .active {
	background-color: #123fab;
}
.url-class .url-class-new {
	overflow: hidden;
}
.url-class .url-class-new .input-text {
	margin: 5px;
	padding: 5px 10px;
}
.url-class .url-class-new .btn {
	margin: 0 5px;
}
.url-class .url-class-new .btn:hover {
	background: #aaa;
}

.url-sites {
	margin-top: 30px;
	width: 100%;
	border: 1px solid #ccc;
}
.site {
	padding: 10px;
	width: 100px;
	text-align: center;
	cursor: pointer;
}
</style>


