<template>
	<div>
		<h3 class="title text-center">js实现快速排序</h3>
		<summary>
			快排算法的实现。
		</summary>
		<div class="sort-carton">
			<strong>快速排序动画</strong>
			<div class="sort-cont">
				<div v-for="one in mergeArr" :style="{height: one+'%'}" class="sort-item">
				</div>
			</div>
			<button class="btn" @click="sort">快速排序</button>
		</div>
		<div>
			<p></p>
		</div>
	</div>
</template>

<style>
.sort-carton {
	padding: 20px;
	text-align: center;
}
.sort-carton .sort-cont {
	height: 300px;
}
.sort-carton .sort-cont .sort-item {
	display: inline-block;
	width: 10px; 
	background-color: pink;
	border: 1px solid #ddd;
}
</style>

<script>
	export default {
		data() {
			return {
					mergeArr: [20, 23, 53, 12, 56, 14, 23, 65, 87, 25, 73, 59]
			}
		},
		methods: {
			merge: function() {

			},
			sort: function() {
				this.quickSort(this.mergeArr, 0, this.mergeArr.length-1, this.mergeArr[0])
			},
			quickSort: function(arr, left, right, key) {
				let dirFlag = true;
				let i = left;
				let j = right;
				if(left >= right || left<0 || right>arr.length) {
					return;
				}
				console.log("left: " + left + ", right: " + right + ", key: " + key);
				console.log("pre sort", arr);
				// setTimeout(function() {
					while(i<j) {
						if(dirFlag) {
							if(arr[j] < key) {
								dirFlag = false;
								let tmp = arr[j];
								arr[j] = arr[i];
								arr[i] = tmp;
							} else {
								j--;
							}
						} else {
							if(arr[i] > key) {
								dirFlag = true;
								let tmp = arr[i];
								arr[i] = arr[j];
								arr[j] = tmp;
							} else {
								i++;
							}
						}
					}
					arr.splice(0,0);
					console.log("end arr", arr);
					console.log("this is one count");
					// arguments.callee(arr, left, i-1, arr[left]);
					// arguments.callee(arr, i+1, right, arr[i+1]);
					arguments.callee(arr, arr, left, i-1, thisarr[left]);
					arguments.callee(arr, arr, i+1, right, thisarr[i+1]);

				// }, 20);
			}
		}
	}

</script>


