import Vue from 'vue'
import Router from 'vue-router'

import mainFrame from '@/components/mainFrame'
import headerSec from '@/components/header'
import sideBarSec from '@/components/siderBar'
import mainSec from '@/components/main'
import webCreamList from '@/components/webCream/list'
import webCream01 from '@/components/webCream/webCream01'
import webCream02 from '@/components/webCream/webCream02'

Vue.use(Router)

var webCream = [
	{
		path: '/',
		name: '',
		component: mainFrame,
		children: [
			{
				path: 'webCream',
				name: 'webCream',
				components: {
			        headPart: headerSec,
			        siderPart: sideBarSec,
			        mainPart: mainSec
				},
				children: [
					{
						path:'list',
						name:'webCream.list',
						component: webCreamList
					},
					{
						path: '01',
						name:'webCream.01',
						component: webCream01
					},
					{
						path: '02',
						name: 'webCream.02',
						component: webCream02
					}
				]
			}
		]
	}
]

export default webCream




