export default {
	getType: function(obj) {
		var str = Object.prototype.toString.call(obj);
		var map = {
			'[object Boolean]': 'boolean',
			'[object Number]': 'number',
			'[object String]': 'string',
			'[object Function]': 'function',
			'[object Array]': 'array',
			'[object Date]': 'date',
			'[object RegExp]': 'regExp',
			'[object Undefined]': 'undefined',
			'[object Null]': 'null',
			'[object Object]': 'object'
		};
		if(obj instanceof Element) {
			return "element";
		}
		return map[str];
	},
	deepCopy: (p) => {
		var obj;
		var str = this.a.getType(p);
		if(str == 'array') {
			obj = [];
			for (var i=0; i<p.length; i++) {
				obj.push(this.a.deepCopy(p[i]));
			}
		} else if(str == 'object') {
			obj = {};
			for(var i in p) {
				obj[i] = this.a.deepCopy(p[i]);
			}
		} else {
			return p;
		}
		return obj;
	},
	copy: (srcData, tarData) => {
		let srcJson = JSON.stringify(srcData);
		var tarObj = JSON.parse(srcJson);
		console.log('api: ', tarObj);
		tarData = tarObj;
	}
}


