import Vue from 'vue'
import Router from 'vue-router'

import bookRouter from './book'
import userRouter from './user'
import webCreamRouter from './webCream'

import mainFrame from '@/components/mainFrame'
import HelloWorld from '@/components/HelloWorld'
import herLover from '@/components/herLover'
import headerSec from '@/components/header'
import sideBarSec from '@/components/siderBar'
import mainSec from '@/components/main.vue'
import booklist from '@/components/book/bookList'
import robot from '@/components/robot/robot'
import notFound from '@/components/404'
import edit from '@/components/adminManage/blogEditor'
import favsites from '@/components/favSites/favsites'


Vue.use(Router)

var mainRoute = [{
      path: '/',
      name: '',
      component: mainFrame,
      children: [
        {
          path: '',
          name: '',
          components: {
            headPart: headerSec,
            siderPart: sideBarSec,
            mainPart: mainSec
          },
          children: [
            {
              path:'',
              component: booklist
            },
            {
              path: 'robot',
              component: robot,
              meta: {
                requireAuth: false
              }
            },
            {
              path: 'edit',
              component: edit,
              meta: {
                requireAuth: true
              }
            },
            {
              path: 'favsites',
              component: favsites,
              meta: {
                title: '网址收藏'
              }
            }
          ]
        }
      ]
    },
    {
      path: '/herLover',
      name: 'herLover',
      component: herLover
    },
    {
      path: '*',
      component: notFound
    }
  ]

mainRoute = mainRoute.concat(bookRouter)
mainRoute = mainRoute.concat(userRouter)
mainRoute = mainRoute.concat(webCreamRouter)

export default new Router({
  mode: 'history',
  routes: mainRoute
})



