<!-- book 列表展示 -->
<template>
	<div class="webCreame">
		<div class="brief">
			看过的好看的书，真的好看。
		</div>
		<div class="webItem" v-for="article in articleList">
			<div to="article.link">
				<router-link :to="article.link">{{article.title}}</router-link></div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
		return {
			articleList: [
				{
					title: "css常用图形实现",
					link: "01"
				},
				{
					title: "js实现快速排序",
					link: "02"
				}
			],
			num: 10
		};
	},
	methods:{

	}

}
</script>

<style scoped>

.bookItem {
	float: left;
	padding: 10px;
	margin: 10px;
	width: 30%;
	height: 350px;
	border: 1px solid #ce6262;
}
.bookItem .cover {
	display: -webkit-flex;
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
	height: auto;
}
.bookItem .cover img {
	width: 200px;
	height: 200px;
}
.bookItem .detail {
	padding: 20px;
	text-align: center;
}
.bookItem .detail .name {
	display: block;
	font: 16px bold;
}
</style>


