<template>
	<div class="login">
		<div class="dialog-cont">
			<div class="dialog-title">
				<span>登陆</span>
				<i class="close iconfont icon-guanbi" @click="close()"></i>
			</div>
			<div class="form">
				<div class="title">
					<span>登陆</span>
				</div>
				<form>
					<label class="label-item">
						<span>邮箱：</span><input type="mail" v-model="mail">
					</label>
					<label class="label-item">
						<span>密码：</span><input type="password" v-model="password">
					</label>
				</form>
			</div>
			<div class="dialog-bottom">
				<div class="btn" @click="login()">登陆</div>
				<div class="btn" @click="close()">取消</div>
			</div>
		</div>
		<div class="dialog-base"></div>
	</div>
</template>

<script>
import nodeRsa from 'node-rsa'
import service from '@/api/service'
import story from '@/api/story'
export default {
	data() {
		return {
			mail: '',
			password: ''
		}
	},
	props: {
		render: Boolean,
	},
	methods: {
		close: function() {
			this.$emit('close');
		},
		login: function() {
			let publicKey = this.getPublicKey();
			if(publicKey) {
				let rsaKey = new nodeRsa(publicKey, 'pkcs8-public');
				let encrypt = {};
				encrypt.mail = rsaKey.encrypt(this.mail, 'base64');
				encrypt.password = rsaKey.encrypt(this.password, 'base64');
				var res = {};
				res.data = encrypt;
				service.login(res).then((result) => {
					console.log(result);
				})
				.catch((err) => {
					console.log('请求失败', err);
				});
			}
			// console.log('key: ', s);
			// encrypt.mail =  s.encrypt(this.mail, 'base64');
			// encrypt.pw = s.encrypt(this.password, 'base64');
			// console.log("input: ", encrypt);

			// var out= {};
			// out.mail = s.decrypt(encrypt.mail, 'utf8');
			// out.pw = s.decrypt(encrypt.pw, 'utf8');
			// console.log('out key: ', out);
		},
		getPublicKey: function() {
			var publicKey = story.localStorage.getLocalStorage('publicKey');
			if(!publicKey) {
				service.getPublicKey().then(function(result) {
					console.log('resolve: ', result.data);
					var ret = result.data;
					if(ret.code === "0000") {
						story.localStorage.setLocalStorage('publicKey', ret.data)
						return ret.data;
					} else {
						return false;
					}
				});
			} else {
				return publicKey;
			}
		}

	}
}
</script>

<style scoped>
.login {
}
.title {
	padding: 10px 0 0;
	text-align: center;
}
.title span {
	font-size: 20px;
}
.dialog-cont {
	position: relative;
	margin: 0 auto;
	z-index: 9999;
	top: 100px;
	width: 400px;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: 5px;
}
.dialog-title {
	padding: 10px;
	background-color: #ddd;
}
.close {
	float: right;
}
.dialog-cont .label-item {
	padding-left: 70px;
	margin: 10px 0;
}

.dialog-bottom {
	display: flex;
	justify-content: center;
	padding: 10px;
}
.btn {
	padding: 5px 20px;
	margin-left: 20px;
	color: #fff;
	background-color: green;
	border-radius: 5px;
}
.btn:hover {
	background-color: red;
}
.dialog-base {
	position: fixed;
	z-index: 50;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: #000;
	opacity: 0.75;
}
</style>


