<template>
  <div>
    <div>
      <span>她喜欢的东西：</span>
      <input type="text" v-model="preferItem" @keyup.enter="add" />
      <div class="btn" @click="add">添加</div>
      <div class="btn" @click="urlgo">跳转首页</div>
      <div class="btn" @click="urlBack">返回</div>
      <router-link to="/">返回首页</router-link>
    </div>
    <div>
      <ul>
        <li v-for="(item, index) of herLoverList" :key="index" @click="del(index)">{{index}} {{item }}
        </li>
      </ul>
    </div>
    <div>
      组件：
      <ul>
        <her-list v-for="(item, index) of herLoverList" :key="index" :content="item" :index="index" @maninfo="del(index)"></her-list>
      </ul>
    </div>
  </div>
</template>
<script>
import list from '@/components/list'
import Router from 'vue-router'
export default {
  name: 'herLover',
  components: {
    'her-list': list
  },
  data() {
    return {
      preferItem: '',
      herLoverList: [],

    }
  },
  methods: {
    add: function() {
      if (this.preferItem == '') {
        return
      }
      this.herLoverList.push(this.preferItem);
      this.preferItem = '';
    },
    del: function(index) {
      this.herLoverList.splice(index, 1);
    },
    urlgo: function() {
      this.$router.push({ path: '/' });
    },
    urlBack: function() {
      this.$router.go(-1);
    }

  }
}

</script>
<style scoped>
.btn {
  padding: 5px;
  width: 50px;
  height: 30px;
  color: white;
  background-color: #45d484;
  border-radius: 10px;
  font-size: 16px;
  line-height: 30px;
  cursor: pointer;
}

.btn:hover {
  background-color: #96c1a9;
}

</style>



