<template>
	<div>
		<div>
			<div>
				<input type="text" v-model="title">
			</div>
			<div>
				<div class="button"></div>
			</div>
		</div>
		<vue-ueditor-wrap v-model="msg"></vue-ueditor-wrap>
	</div>
</template>

<script>
import VueUeditorWrap from 'vue-ueditor-wrap'

export default {
	data(){
		return {
			msg: ''
		}
	},
	components: {
		VueUeditorWrap
	}
}
</script>


