<!-- book 列表展示 -->
<template>
	<div class="bookList">
		<div class="">
			看过的好看的书，真的好看。
		</div>
		<div class="bookItem" v-for="book in bookList">
			<div class="cover">
				<img :src="book.imgUrl">
			</div>
			<div class="detail">
				<span class="name">{{ book.name }}</span>
				<div class="book-de">			
					<span class="author">{{ book.author }}</span>
					<span class="time">{{book.publicDate}}</span>
				</div>
				<span class="type">{{book.type}}</span>
				<span class="label" v-for="item in book.label">{{item}}</span>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
		return {
			bookList: [
				{
					name: '三体',
					imgUrl: 'http://img.hb.aicdn.com/1296a9c3d6f87d2ef40b35fbec4c67755e7b175846ec7-mUlV1V_fw658',
					author: '刘慈欣',
					publicDate: '2008-01',
					type: '科幻',
					label: ['大气', '超前', '宏观']
				},
				{
					name: '活着',
					imgUrl: 'http://img3m9.ddimg.cn/93/8/25092039-1_w_3.jpg',
					author: '余华',
					publicDate: '1993-01',
					type: '科幻',
					label: ['大气', '超前', '宏观']
				},
				{
					name: '兄弟',
					imgUrl: 'http://img3m4.ddimg.cn/12/35/25233924-1_w_6.jpg',
					author: '余华',
					publicDate: '2005-06',
					type: '科幻',
					label: ['大气', '超前', '宏观']
				},
				{
					name: '许三观卖血记',
					imgUrl: 'http://img3m3.ddimg.cn/36/0/25165143-3_w_2.jpg',
					author: '余华',
					publicDate: '1998-01',
					type: '科幻',
					label: ['大气', '超前', '宏观']
				},
				{
					name: '小王子',
					imgUrl: 'http://img3m9.ddimg.cn/75/6/25067469-1_w_2.jpg',
					author: '安托万·德·圣·埃克苏佩里',
					publicDate: '1942-10',
					type: '科幻',
					label: ['大气', '超前', '宏观']
				}
			],
			num: 10
		};
	},
	methods:{

	}

}
</script>

<style scoped>

.bookItem {
	float: left;
	padding: 10px;
	margin: 10px;
	width: 30%;
	height: 350px;
	border: 1px solid #ce6262;
}
.bookItem .cover {
	display: -webkit-flex;
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
	height: auto;
}
.bookItem .cover img {
	width: 200px;
	height: 200px;
}
.bookItem .detail {
	padding: 20px;
	text-align: center;
}
.bookItem .detail .name {
	display: block;
	font: 16px bold;
}
</style>


