import Vue from 'vue'
import axios from 'axios'
import jsonp from 'jsonp'
import http from 'http'
import $ from 'jquery'

const serverHost = "http://localhost:4000";

export default {
	// robot机器人自动聊天
	robotSendtext: data => {
		return axios.get(serverHost + '/common/robotDialog', data);
	},
	login: data => {
		return axios.post(serverHost + '/login/certifyConfirm', data);
	},
	// 请求公钥
	getPublicKey: () => {
		return axios.get(serverHost + '/login/getPublicKey');
	},
	// 添加分类
	addUrlClass: data => {
		return axios.post(serverHost + '/favsites/addUrlClass', data);
	},
	// 添加site
	addSite: data => {
		return axios.post(serverHost + '/favsites/addSite', data);
	},
	// 查询sites分类
	getAllSitesDetail: () => {
		return axios.post(serverHost + '/favsites/searchSitesDetail');
	}

}


