<!-- 网站页面头部 -->

<template>
	<div class="header">
		<div class="title">
			<span>This is my house.</span>
		</div>
		<div class="logo">
			<router-link to="/"><img src="@/assets/avatar.jpg" alt="头像"></router-link>
		</div>
	</div>
</template>

<script>
export default {
	name: 'headerSec'
}
</script>

<style scoped>
.header {
	width: 100%;
	height: 100px;
	background-color: #8dc3b4;
}
.title {
	float: left;
	width: 60%;
	height: 100%;
	padding: 30px 20px;
}

.logo {
	float: right;
	height: 100%;
	padding: 20px;
}
.logo img {
	height: 94%;
	border-radius: 50%;
}
</style>


