<!-- 问答机器人 -->
<template>
	<div>
		<div class="text-input">
			<textarea v-model="contSend.text" @keyup.enter="sendText()"></textarea>
			<div>
				<div class="button" @click="sendText()">发  送</div>
			</div>
		</div>
		<div class="dialog overflow-auto" id="conver-list">
			<div v-for="(item, index) in dialogList" :key="index" class="overflow-auto">
				<div v-if="item.flag === 'custom'" class="custom-dialog conver">
					<div class="cont">{{item.cont}}</div>
					<div class="ava">
						<img src="@/assets/avatar.jpg" alt="It's you">
					</div>
				</div>
				<div v-if="item.flag === 'robot'" class="robot-dialog conver">
					<div class="ava">
						<img src="@/assets/robot.png" alt="robot">
					</div>
					<div class="cont">{{item.cont}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import service from '@/api/service'
import $ from 'jquery'
export default {
	data(){
		return {
			dialogList:[],
			contSend: {
				text: ''
			},
			contReceive: '',
		};
	},
	methods: {
		sendText: function () {
			let apiToken = 'c9b1873ef3e64ac1bad39508973ebadc';
			var contItem = {
				flag: 'custom',
				cont: this.contSend.text,
				time: new Date(),
			};
			this.dialogList.push(contItem);
			let data = {
				params: {
					info: this.contSend.text
				}
			}
			this.contSend.text = '';
			// service.robtoJsonp(data.params);

			service.robotSendtext(data)
				.then(result=>{
					let ret = result.data;
					if(ret.error_code === 0) {
						var contRes = {
							flag: 'robot',
							cont: ret.result.text,
							time: new Date()
						};
						this.dialogList.push(contRes);
					} else {
							var contRes = {
							flag: 'robot',
							cont: '风好大，我听不到啊！',
							time: new Date()
						};
						this.dialogList.push(contRes);
						console.log('request error.')
					}
				})
				.catch(error=>{
					console.log('error',error);
				});
		},
		// 确保聊天内容自动显示最新的内容，即滚动条滚动到最下方
		viewBottom: function() {
			this.$nextTick(() => {
				var div = $('#conver-list')[0];
				div.scrollTop = div.scrollHeight;
			});
			console.log('viewBottom');
		}
	},
	watch: {
		'dialogList': 'viewBottom'
	}
}
</script>

<style scoped>
.dialog {
	position: relative;
	float: left;
	padding: 10px;
	margin: 20px;
	width: 500px;
	height: 500px;
	background-color: #f0f6eb;
}

.text-input {
	float: left;
	width: 300px;
	height: auto;
}
.text-input > textarea {
	margin: 20px 0;
	height: 200px;
	width: 100%;
	font-size: 20px;
	color: #235413;

	resize: none
}
.button {
	position: relative;
	float: right;
	margin: 0 20px;
	padding: 10px;
	font-size: 16px;
	color: #ddd;
	cursor: pointer;
	background-color: #237871;
	border: 1px solid #237871;
	border-radius: 5px;
}
.button:hover {
	background-color: #ab23da;
	border-color: #ab23da;
}

.cont {
	float: left;
	padding-top: 10px;
	width: calc(100% - 50px);
}
.ava {
	float: left;
	padding: 5px;
	width: 50px;
}
.ava img {
	width: 100%;
	border-radius: 50%;
}
.custom-dialog {
	float: right;
}

.robot-dialog {
	float: left;
}
.conver {
	max-width: 70%;
}
.overflow-auto {
	overflow: auto;
}
</style>


