import Vue from 'vue'
import Router from 'vue-router'

import mainFrame from '@/components/mainFrame'
import headerSec from '@/components/header'
import sideBarSec from '@/components/siderBar'
import mainSec from '@/components/main'
import booklist from '@/components/book/bookList'

Vue.use(Router)

var book = [
	{
		path: '/',
		name: '',
		component: mainFrame,
		children: [
			{
				path: 'book',
				name: 'book',
				components: {
			        headPart: headerSec,
			        siderPart: sideBarSec,
			        mainPart: mainSec
				},
				children: [
					{
						path:'list',
						name:'list',
						component: booklist
					}
				]
			}
		]
	}
]

export default book




