// 用户登陆注册路由

import userFrame from '@/components/user/userFrame'
import login from '@/components/user/login'

var userRouter = [
	{
		path: '/user',
		component: userFrame,
		children: [
			{
				path: 'login',
				component: login
			}
		]
	}
]

export default userRouter


