    <login-dialog v-show="global.loginFlag" @close="closeDialog()"></login-dialog>
  </div>
</template>

<script>
import loginDialog from '@/components/user/login'
import global from './api/global'
export default {
  data() {
    return {
      global: global,
    }
  },
  components: {
    loginDialog
  },
  mounted: function(){
    this.init();
  },
  methods: {
    init: function() {

    },
    closeDialog: function() {
      console.log(global);
      global.loginFlag = false;
    }
  },
  watch: {
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  overflow: auto;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
